<template>
        <div id="yo" class="float-center" @mouseover="showComment($event, true)" @mouseleave="showComment($event, false)" v-if="showIf">
            <button class="btn btn-default" @click="leftScroll"><span class="glyphicon glyphicon-arrow-left"></span></button>
            <button class="btn btn-default" @click="rightScroll"><span class="glyphicon glyphicon-arrow-right"></span></button>
        </div>
</template>

<script>
export default {
    props :['templateId'],
    data(){
        return{
            floatCenter:'float-center',
            floatTop:'float-top',
            showIf:true,
            hoverd:false
        };
    },
    methods :{
        leftScroll(){
            let item=$('#'+this.templateId).scrollLeft();
            $('#'+this.templateId).animate({scrollLeft: item-350+'px'}, 500);
            console.log($('#'+this.templateId).scrollLeft())
        },
        rightScroll(){
            let item=$('#'+this.templateId).scrollLeft();
            $('#'+this.templateId).animate({scrollLeft: item+350+'px'}, 500);
        },
        handleScroll(){
          if($('#'+this.templateId).offset().top) {
              var dist1 = $('#' + this.templateId).offset().top - $(window).scrollTop() - $('.navbar').height();
              var dist2 = $('#' + this.templateId).offset().top + $('#' + this.templateId)[0].offsetHeight - $(window).scrollTop() - $('.navbar').height();
              if (dist1 <= 0 && dist2 > 0) {
                this.showIf=true;
              } else {
                this.showIf=false;
              }
          }
        },
        showComment: function(event, show) {
          if (show === false) {
            $(event.currentTarget).tooltip("destroy");
            this.hoverd = false;
            return;
          }
          if (
            this.hoverd == true
          ) {
            return;
          }
          this.hoverd = true;
          $(event.currentTarget)
            .tooltip({
              placement: "top",
              html: true,
              title: "左右滑动表格"
            })
            .tooltip("toggle");
        },
    },
    mounted(){
        window.addEventListener('scroll', this.handleScroll);
    },
    beforeDestroy(){
        window.removeEventListener('scroll',this.handleScroll);
    },

}
</script>

<style scoped>
    .float-center{
        opacity:0.6;
        display:block;
        position:fixed;
        right:0;
        top:50%;
        bottom:50%;
        z-index:5;
        margin-right:20px;
    }
    .hover-tip{
        position:absolute;
        top:0px;
        right:120px;
        width:120px;
        height:100%;
    }
</style>


