import moment from 'moment';
import lodash from 'lodash';
import allocConf from "@/config/AllocConditionConf";

export default {
    generateCreateExpName: function (strategySetName) {
        return strategySetName + '实验_' + moment().format('YYYYMMDD_HHMM');
    },
    collectTrueItem: function (allList) {
        var res = [];
        for (var index in allList) {
            if (allList[index]) {
                res.push(index);
            }
        }
        return res;
    },
    getStrLen: function (str) {
        if (str == null) {
            return 0;
        }
        if (typeof str != "string") {
            str += "";
        }
        let newStr = str.replace(/[^\x00-\xff]/g, "01");
        newStr = newStr.replace(/（|）/g, "01");
        return newStr.length;
    },
    cleanArray: function (actual) {
        const newArray = [];
        for (let i = 0; i < actual.length; i++) {
            if (actual[i]) {
                newArray.push(actual[i]);
            }
        }
        return newArray;
    },
    fmtStrategySetList: function (data) {
        var that = this;
        lodash.forEach(data, function (val, index) {
            var restDotCount = 38 - that.getStrLen(val.strategy_set_name);
            val.strategy_set_name_new = val.strategy_set_name + lodash.padEnd('', restDotCount, '·') + val.strategy_set_comment.substring(0, 40);
        });
        return data;
    },
    fmtExpLayerList: function (data) {
        var that = this;
        lodash.forEach(data, function (val, index) {
            var restDotCount = 38 - that.getStrLen(val.exp_layer_name);
            val.exp_layer_name_new = val.exp_layer_name + lodash.padEnd('', restDotCount, '·');
        });
        return data;
    },
    obj2QueryString: function (obj) {
        if (!obj) return "";
        return this.cleanArray(
            Object.keys(obj).map(key => {
                if (obj[key] === undefined) return "";
                return encodeURIComponent(key) + "=" + encodeURIComponent(obj[key]);
            })
        ).join("&");
    },
    showDomain: function (layer) {
        let expZoneCodeStr = layer.exo_zone_code + '';
        return expZoneCodeStr.indexOf('783') > -1;
    },
    isOver30Days: function (from, to) {
        return moment(from).isBefore(moment(to).subtract(30, 'days').format('YYYY-MM-DD h:mm:ss'));
    },
    jumpIndex: function () {
        window.location.href = '/experiment';
    },
    reload: function () {
        window.location.reload();
    },
    jump: function (url) {
        window.location.href = url;
    },
    formateAllocConditionValue(key, val) {

        
        if (key == 'GENDER') {
            return allocConf.GenderConf[val]
        }
        
        let list = val.split(',')
        if (key == 'AGE') {
            return `从${list[0] % 1000}到${list[1] % 1000}`
        }
        

        let formatList = []
        if (key == 'PLATFORM') {
            list.forEach(item => {
                formatList.push(allocConf.PlatformConf[item])
            })
            return formatList.join('、')
        }
        if (key == 'SERVICE') {
            list.forEach(item => {
                formatList.push(allocConf.SceneService[item])
            })
            return formatList.join('、')
        }

        if (key == 'LOCATION') {
            list.forEach(item => {
                if(allocConf.City.HotCity[item] != undefined) {
                    formatList.push(allocConf.City.HotCity[item])
                } else if(allocConf.City.OtherCity[item] != undefined) {
                    formatList.push(allocConf.City.OtherCity[item])
                }
            })
            return formatList.join('、')
        }

        if (key == 'IP') {
            return val;
        }

        if (key == 'VERSION') {
            return `大于` + list[1].replace(/\,/, "")
        }
    },
    formateCustExpObject : function(layer) {
        
        if (
            layer.exp_layer_type == 1 &&
            layer.is_same_type > 0
        ) {
            let allocVal = JSON.parse(layer.alloc_condition_value)
            let expObjTypeName = Object.keys(allocVal)[0]
            let expObjTypeText =  allocConf.CustObjectTypeMap[expObjTypeName]
            return [{
                name : expObjTypeText ,
                data : allocVal[expObjTypeName],
                name_code : expObjTypeName,
            }]
        }

        return []
    },
    foramatAllocCondition: function (conditionVal) {
        let retData = []
        Object.keys(conditionVal).forEach(val => {
            let data = conditionVal[val];
            let tmp = {
                name: allocConf.AllocConditionMap[val],
                data: this.formateAllocConditionValue(val, data)
            }
            retData.push(tmp)
        });
        return retData
    },
    unZipAlloCondtion(condition) {
        let retData = {}

        if ( Object.keys(condition).length < 1) {
            return {}
        }
        Object.keys(condition).forEach(key => {
            if (key == "GENDER") {
                retData.GENDER = parseInt(condition[key]);
            } else if (key == "AGE") {
                let tmp = condition[key].split(',')
                retData.AGE = [parseInt(tmp[0]) - 1000, parseInt(tmp[1]) - 1000]
            } else if (key == "VERSION") {
                retData.VERSION =  condition[key].split(',')[1]
            } else {
                retData[key] = condition[key].split(',')
            }
        });

        if (retData.AGE == undefined) {
            retData.AGE = [1, 100];
        }
        return retData
    },
    zipAllocCondition(condition) {
        let retData = {}
        console.log(condition)
        Object.keys(condition).forEach(key => {
            if (key == "VERSION") {
                if (condition[key] != "") {
                    retData[key] = `>=,${condition[key]}`
                }
            } else if (key == "GENDER"){
                if (condition[key] != "0") {
                    retData[key] = condition[key]
                }
            } else if (condition[key].length > 0) {
                retData[key] = condition[key].join(",");
            }
        });
        if (retData.AGE == "1,100") {
            lodash.unset(retData, "AGE");
        } else {
            let age = retData.AGE.split(",")
            retData.AGE = [1000 + parseInt(age[0]), 1000 + parseInt(age[1])].join(",")
        }

        if (retData.GENDER != undefined ) {
            retData.GENDER = "" + retData.GENDER
        }
        
        return retData;
    },
    getAllocConfDisplayData: function (jsonStr) {
        let conditionVal = JSON.parse(jsonStr)
        let retData = []
        Object.keys(conditionVal).forEach(val => {
            let data = conditionVal[val];
            let tmp = {
                name: allocConf.AllocConditionMap[val],
                data: this.formateAllocConditionValue(val, data)
            }
            retData.push(tmp)
        });
        return retData
    },
}