<template>
    <div ref="json">
        <div class="text-right">
            <button @click="copyJson" class="btn btn-default btn-xs">点我复制</button>
        </div>
        <div v-html="showString"></div>
    </div>
</template>

<script>
export default {
    props: ["showStr"],
    data() {
        return {
            showString: ""
        };
    },
    watch: {
        showStr: {
            handler: function(val, oldVal) {
                this.showString = this.fmtShowJson(val);
            }
        }
    },
    mounted: function() {
        this.showString = this.fmtShowJson(this.showStr);
    },
    methods: {
        fmtShowJson: function(jsonStr) {
            //alert(jsonStr);
            if (jsonStr == "" || jsonStr == "{}") {
                return "";
            }
            jsonStr = JSON.stringify(JSON.parse(jsonStr), null, 4);

            jsonStr = jsonStr
                .replace(/&/g, "&")
                .replace(/</g, "<")
                .replace(/>/g, ">");

            jsonStr = jsonStr.replace(
                /("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g,
                function(match) {
                    var cls = "number";
                    if (/^"/.test(match)) {
                        if (/:$/.test(match)) {
                            cls = "color: #92278f;";
                        } else {
                            cls = "color: green;";
                        }
                    } else if (/true|false/.test(match)) {
                        cls = "color: blue;";
                    } else if (/null/.test(match)) {
                        cls = "color: magenta;";
                    }
                    return '<span style="' + cls + '">' + match + "</span>";
                }
            );

            return '<pre style="text-align:left;background:white;border:none">' + jsonStr + "</pre>";
        },
        copyJson() {
             this.$copyText(this.showStr, this.$refs.json).then((e) => {
                this.$message('复制成功')
            }, () => {
                alert('复制失败')
            })
        }
    }
};
</script>
