export default {
    state: {
        exp_layer_type : -1,
        strategy_set_id : 0,
        exp_zone_code : 78301,
        is_same_type : 1,

        flow_alloc_conf : {
            AGE: [1, 100],
            GENDER: '0',
            PLATFORM: [],
            SERVICE: [],
            LOCATION: [],
            VERSION: "",
            IP: []
        },
        server_name : "",
        exp_base_info : {},
        exp_list :[],

        del_exp_list : [], //实验编辑的时候用
    },
    getters: {
        show_domain(state) {//这里的state对应着上面这个state
            return state.exp_zone_code == 78301 && state.exp_layer_type == 0
        },
    },
    actions : {
        clearExpInfo(context) {
            context.commit('clearData')
        },
        clearDelExps(context) {
            context.commit('setDelExpEmpty')
        }
    },
    mutations: {
        clearData(state) {
            state.exp_list = []
            //state.strategy_set_id = 0
            state.exp_base_info = {},
            state.flow_alloc_conf = {
                AGE: [1, 100],
                GENDER: "0",
                PLATFORM: [],
                SERVICE: [],
                LOCATION: [],
                VERSION: "",
                IP: []
            }
        },
        setExpZoneCode(state, code) {
            state.exp_zone_code = code
        },
        setExpLayerType(state, t) {
            state.exp_layer_type = t;
        },
        setSameType(state, t) {
            state.is_same_type = t;
        },
        setStrategySetId(state, id) {
            state.strategy_set_id = id;
        },
        setFlowAlloConf(state, conf) {
            state.flow_alloc_conf = conf;
        },
        setServerName(state, name) {
            state.server_name = name;
        },
        setExpBaseInfo(state, info) {
            state.exp_base_info = info;
        },
        setExpList(state, list) {
            state.exp_list = list;
        },
        setDelExp(state, exp) {
            exp.is_del = 'true'
            state.del_exp_list.push(exp);
        },
        setDelExpEmpty(state, exp) {
            state.del_exp_list = []
        }
    }
}