<template>
<span style="cursor:pointer" 
    @mouseover="showPopover($event)"
    @mouseleave="hidePopover($event)"
    @click="hidePopover($event)">
    <slot>{{showString}}</slot>
</span>
</template>

<script>

export default {
	props : ['length', 'showStr', 'popStr'],
    computed : {
        showString : function() {

            if(this.showStr == undefined) {
                return null;
            }

            if(this.length == undefined) {
                return this.showStr;
            }

            if(this.$tool.getStrLen(this.showStr) > this.length) {
                return this.showStr.substring(0, this.length) + '...';
            }

            return this.showStr;
            
        }
    },
    methods : {
        showPopover : function(event) {
            if(this.$tool.getStrLen(this.showStr) < this.length) {
                return false
            }
            let popContent = this.popStr || this.showStr;
            $(event.currentTarget).popover({
                placement : 'top',
                html : true,
                delay : 0,
                content : '<p class="p-break">' + popContent + '</p>',
                container: 'body'
            }).popover('toggle');
        },
        hidePopover : function(event) {
            $(event.currentTarget).popover('destroy');
        },
    }
}
</script>
