import lodash from 'lodash';
export default {
    state:{
        username : '',
        user_type : '',
        assign_auditor : [],
        headbreadcrumb : "",
    },
    mutations:{
        setUsername(state, name){
            state.username = name;
        },
        setUserType(state, type){
            state.user_type = type;
        },
        initAssignUser(state, users){
            state.assign_auditor = users;
        },
        setAssignUser(state, user) {
            state.assign_auditor = user;
        },
        addAssignUser(state, user){
            state.assign_auditor.push(user);
        },
        delAssignUser(state, user) {
            lodash.pull(state.assign_auditor, user);
        },
        setHeadBreadCrumb(state, b) {
            state.headbreadcrumb = b
        }
    }
}