<template>
<div>
    <div :id="chartId" style="height:400px;width: 100%;">

    </div>
</div>
</template>

<script>

import lodash from 'lodash';
import echarts from 'echarts';
require('echarts/theme/vintage')
require('echarts/theme/dark')
require('echarts/theme/infographic')
require('echarts/theme/macarons')
require('echarts/theme/shine')
import ChartConfig from '@/config/ChartConfig';
const lineColorConfig = [

];
export default {
    props : ['chartId', 'chartData', 'dataType', 'yform', 'showToolBox','lengendPosition','percentItem'],
    watch : {
        chartData : {
            handler : function(val) {
                this.drawChart(val);
            },
            deep : true
        },
    },
    mounted(){
        this.drawChart(this.chartData);
    },
    methods:{
        drawChart : function(chartData) {

            var myChart = echarts.init(document.getElementById(this.chartId), 'shine');

            let initOption = ChartConfig.option;
            initOption.title.text = chartData.title;
            initOption.title.show = false;

            initOption.toolbox.show = true;
            if(this.showToolBox != undefined || this.showToolBox != null) {
                initOption.toolbox.show = this.showToolBox;
            }
            

            if(this.dataType == 'bar') {
                initOption.xAxis = { //横坐标，时间
                    data : chartData.xAxis || [],
                    axisLabel:{
                        rotate : 10,//倾斜度 -90 至 90 默认为0
                        margin:15,
                        textStyle:{
                            fontWeight:"bolder",
                            color:"#000000"
                        }
                    },
                };
            } else {
                initOption.xAxis = { //横坐标，时间
                    data : chartData.xAxis || [],
                    boundaryGap: false,
                };
            }
            

            let lineTitle = chartData.data?Object.keys(chartData.data):[];

            initOption.legend = {
                data : lineTitle, //多少条线，每条线的名字
                selected : {},
                type : 'scroll',
            };

            if(this.lengendPosition=='top'){
                initOption.legend = {
                    data : lineTitle, //多少条线，每条线的名字
                    selected : {},
                    type : 'scroll',
                    top : 10
                };
            }
            else if(this.lengendPosition == 'bottom'||this.lengendPosition == null || this.lengendPosition == ''){
                initOption.legend = {
                    data : lineTitle, //多少条线，每条线的名字
                    selected : {},
                    type : 'scroll',
                    bottom :10
                };
            }

            let lineData = [];
            let that = this;
            lineTitle.forEach( function(element, index) {
                lineData.push({
                    data : chartData.data[element] || [],
                    name : element,
                    type : that.dataType || 'line',
                    smooth : true,
                    lineStyle:{
                        //color:'#00FF00'
                    },                     
                })
            });

            if( this.yform == '%') {
                initOption.yAxis.axisLabel.formatter = '{value} %';
                if(this.percentItem){
                    let str='<br/>{a0}: {c0}%<br/>{a1}: {c1}%<br/>{a2}: {c2}%<br/>{a3}: {c3}%'
                    let res=str.slice(0,parseInt(this.percentItem)*16);
                    initOption.tooltip.formatter='{b0}'+res
                }
            } else if(this.yform == 'thousand') {
                initOption.yAxis.axisLabel.formatter = function(value) {
                    return value /1000 + '千';
                }
                initOption.tooltip.formatter=false;
            } else if(this.yform == '10thousand') {
                initOption.yAxis.axisLabel.formatter = function(value) {
                    return value /10000 + '万';
                }
                initOption.tooltip.formatter=false;
            } else if(this.yform == 'million') {
                initOption.yAxis.axisLabel.formatter = function(value) {
                    return value /1000000 + '百万';
                }
                initOption.tooltip.formatter=false;
            } else if(this.yform == 'billion') {
                initOption.yAxis.axisLabel.formatter = function(value) {
                    return value /1000000000 + '亿';
                }
                initOption.tooltip.formatter=false;
            } else if(this.yform != '' && this.yform != null){
                initOption.tooltip.formatter=false;
                initOption.yAxis.axisLabel.formatter = '{value}' + this.yform;
            } else {
                initOption.yAxis.axisLabel.formatter = '{value} ';
                initOption.tooltip.formatter=false;
            }

            initOption.series = lineData;
            if(this.dataType != 'bar') {
                initOption.color = [ "#0098d9","#e6b600", "#c12e34" , "#2b821d", "#005eaa", "#339ca8", "#925317", "#bba1d8"];
            } else {
                initOption.color = [ "#c12e34", "#e6b600", "#0098d9", "#2b821d", "#005eaa", "#339ca8", "#cda819", "#32a487"];
            }
            
            myChart.clear();
            myChart.setOption(initOption);
            setTimeout(() => {
                myChart.resize();
            }, 500)
            
            myChart.on("click", this.clickPiePart);
        },
        clickPiePart : function(param) {
            this.$emit('clickPiePart', param);
        }
    }
}
</script>
