import lodash from 'lodash';
import moment from 'moment';
export default {
    state: {
        exp_layer: {},
        base_exp_id : '',
        time_area: [
            moment().format('YYYY-MM-DD HH:mm:00'),
            moment().format('YYYY-MM-DD HH:mm:00')
        ],
        query: {
            platform: [],
            bid_type: [],
            promotion_objective: [],
            creative_style_id: [],
            psid: [],
            optimization_goal: [],
        },
        group_by: 'psid',
        load_state: false,

        //广告主侧非同质实验
        exp_list : [],
        exp_id : "",
        unexp_time_area : [
            moment().format('YYYY-MM-DD HH:mm:00'),
            moment().format('YYYY-MM-DD HH:mm:00')
        ],
    },
    mutations: {
        setExpLayer(state, layer) {
            state.exp_layer = layer;
            state.base_exp_id = '';
        },
        setBaseExpId(state, id) {
            state.base_exp_id = id;
        },
        setQueryConfig(state, config) {
            state.query = config
        },
        selectGroupBy(state, groupBy) {
            state.group_by = groupBy;
        },
        setLoadState(state, st) {
            state.load_state = st;
        },
        setTime(state, t) {
            state.time_area = t
        },

        //广告主侧非同质实验特有
        setExpList(state, list) {
            state.exp_list = list
        },
        setExpId(state, id) {
            state.exp_id = id
        },
        setUnExpTime(state, t) {
            state.unexp_time_area = t
        },
    }
}